.title {
	margin: 30px;
	text-align: center;
	font-family: fangsong;
	font-weight: 700;
	font-size: calc(26px + (56 - 26) * ((100vw - 320px) / (1432 - 320)));
	margin: calc(10px + (30 - 10) * ((100vw - 320px) / (1432 - 320)));
	/* letter-spacing: 0.2vw; */
}

.services {
	margin-top: calc(30px + (60 - 30) * ((100vw - 320px) / (1432 - 320)));
	padding-bottom: calc(30px + (60 - 30) * ((100vw - 320px) / (1432 - 320)));
	margin-right: calc(10px + (30 - 10) * ((100vw - 320px) / (1432 - 320)));
	margin-left: calc(10px + (30 - 10) * ((100vw - 320px) / (1432 - 320)));
}

.works {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

.col {
	flex-basis: 50%;
	/* padding: 15px; */
	padding: calc(10px + (15 - 10) * ((100vw - 320px) / (1432 - 320)));

	display: flex;
	flex-direction: column;
	/* flex-wrap: wrap; */

	@media (max-width: 767px) {
		flex-basis: 100%;
	}
}

.item {
	padding: 5px;
	box-shadow: 0 0 4px rgba(12, 12, 12, 0.3);
	border-radius: 15px;
	transition: box-shadow 0.2s linear;
	flex: 1;
	cursor: pointer;
}

.item:hover {
	box-shadow: 0 0 25px rgba(128, 128, 128, 0.3);
}
