.wrapper {
	width: 100%;
	margin: 0 auto;
	text-align: center;
}

.slick-prev {
	left: 0;
	z-index: 50;
	&:hover {
		box-shadow: none;
	}
	min-width: 20px;
}
.slick-prev:before {
	font-size: 40px;
	margin-left: 10px;
	/* color: #2d2e31; */
}

.slick-next {
	right: 20px;
	min-width: 0;
	/* z-index: 50; */
	&:hover {
		box-shadow: none;
	}
}

.slick-next:before {
	font-size: 40px;
	margin-left: -10px;
	/* color: #2d2e31; */
}

.slick-dots li button {
	min-width: 0;
	&:hover {
		box-shadow: none;
	}
}

.slick-dots li button:before {
	font-size: 10px;
}

.slider__item {
	height: calc(200px + (600 - 200) * ((100vw - 320px) / (1432 - 320)));
	width: 100%;
	background-color: #937276;
	cursor: pointer;
	position: relative;
	overflow: hidden;
	margin: 0;
}

.slider__title {
	font-family: fangsong;
	font-size: calc(14px + (45 - 14) * ((100vw - 320px) / (1432 - 320)));
	color: #ffff;
	position: absolute;
	top: 50%;
	left: 50%;
	z-index: 5;
}

.slider__img {
	display: block;
	max-width: 100%;
	max-height: 100%;
	/* width: 100%;
	height: 100%; */
	position: absolute;
	top: 0;
	left: 0;
	object-fit: cover;
	/* transition: transform 400ms ease-in-out; */
	/* &:hover {
		transform: scale(1.5);
	} */
}

.wrapper .slick-track {
	display: flex;
	align-items: flex-start;
	padding-top: 0;
}

.sliderOne .slick-list,
.slick-slider,
.slick-track {
	padding-top: 0 !important;
	padding-bottom: 0 !important;
}

