@media (max-width: 767px) {
	.slick-next:before {
		font-size: 20px;
		color: #ffff;
	}
	.slick-prev:before {
		font-size: 20px;
		color: #ffff;
	}
	.slick-next {
		right: 10px;
	}

	.slick-prev {
		left: 10px;
	}

	.slick-list,
	.slick-slider,
	.slick-track {
		/* padding: 0.5rem 0; */
	}
}
